import React from 'react'
import { graphql } from 'gatsby'
import Header from '../components/Header'
import Seo from '../components/Seo'
import PhotosIndexLayout from '../layouts/PhotosIndexLayout'
import AspectRatio from '../services/AspectRatio'

const PhotosPage = async ({ data }) => {
  const photos = data.allPrismicPhoto.edges.reduce((photos, {node}) => {
    const { id, slugs, data } = node;
    const path = slugs[0]
    const imageExif = data.photo_file.localFile.fields.exif
    const imageSize = { width: imageExif.exif.PixelXDimension, height: imageExif.exif.PixelYDimension }
    const aspectRatio = AspectRatio.getAspectRatio(imageSize)

    const photoData = {
      id,
      slug: path,
      exif: imageExif,
      title: data.title,
      aspectRatio,
      description: data.photo_description,
      file: data.photo_file,
      instagram: data.instagram
    }

    photos.push(photoData);
    return photos;
  }, []);

  return (
    <PhotosIndexLayout photos={photos}>
      <Seo title="Photos" description="A collection of photos by Kashi Samaraweera" />
      <Header siteTitle="Photos" subtitle="shutterbug" />
    </PhotosIndexLayout>
  )
}

export const photoQuery = graphql(
  `fragment childImageSharpFluid on ImageSharpFluid {
    base64
    aspectRatio
    src
    srcSet
    srcWebp
    srcSetWebp
    sizes
  }

  fragment exifData on File {
    fields {
      exif {
        exif {
          PixelXDimension
          PixelYDimension
          ExposureTime
          FNumber
          FocalLength
          ISO
          DateTimeOriginal
          DateTimeDigitized
        }
        image {
          Make
          Model
          Orientation
        }
        gps {
          GPSLatitude
          GPSLatitudeRef
          GPSLongitude
          GPSLongitudeRef
          GPSAltitude
          GPSTimeStamp
          GPSDateStamp
        }
      }
    }
  }

  query SitePhotoListQuery {
    allPrismicPhoto {
      edges {
        node {
          id
          slugs
          data {
            title {
              text
            }
            photo_description {
              html
            }
            photo_file {
              localFile {
                ...exifData
                childImageSharp {
                  fluid(maxWidth: 2560) {
                    ...childImageSharpFluid
                  }
                }
              }
              url
            }
            instagram {
              url
            }
          }
        }
      }
    }
  }`)

export default PhotosPage
